<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <span class="secondtitle">所有用户</span>

        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-input placeholder="请输入被审核着姓名" style="left: 200px;position: relative;width: 200px;">
        </el-input>
        <el-button icon="el-icon-search" type="primary" style="left: 200px;position: relative;">搜索</el-button>
        <br><br>

        <el-row class="ep_info">

          <el-table  :data="tableData" style="width: 800px;left: 200px;position: relative;">

            <el-table-column  label="公司编号">
              <template slot-scope="scope">
                <span class="col-cont" v-html="showDate(scope.row.company_id)" ></span>
              </template>

            </el-table-column>
            <el-table-column  label="员工编号">
              <template slot-scope="scope">
                <span class="col-cont" v-html="showDate(scope.row.staff_id)" ></span>
              </template>
            </el-table-column>
            <el-table-column  label="是否审核">
              <template slot-scope="scope">
                <span class="col-cont" v-html="showDate(scope.row.status)" ></span>
              </template>
            </el-table-column>
            <el-table-column  label="是否通过">
              <template slot-scope="scope">
                <span class="col-cont" v-html="showDate(scope.row.ispass)" ></span>
              </template>
            </el-table-column>
          </el-table>

        </el-row>

      </div>
    </div>
  </div>
</template>

<script>
import {getAllcheckData} from "../../../service/allUsers";

export default {
  data(){
    return{
      loading:true,
      tableData:[],
      search:''
    }
  },
  mounted() {
    this.gettableData()
  },
  computed: {
    // 实时监听表格
    tables: function() {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  },
  methods: {
    // 筛选变色
    showDate(val) {
      val = val + '';
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<span style="color: red; ">' + this.search + '</span>')
      } else {
        return val
      }
    },
    gettableData(){
      getAllcheckData().then((res)=>{
        this.tableData=res.data.staffs
        for (let i=0;i<this.tableData.length;i++){
          if (this.tableData[i].ispass==1){
            this.tableData[i].ispass='已通过'
          }else {
            this.tableData[i].ispass='否'
          }
          if (this.tableData[i].status==0){
            this.tableData[i].status='否'
          }else{
            this.tableData[i].status='已审核'
          }
        }
        this.loading=false
        console.log(res.data.staffs)
      }).catch(err=>{
        return this.$message.error(err.message)
      })
    },
  }
}
</script>

<style scoped>

</style>



